.app {
  @include themed() {
    color: t($text-color);
    background-color: t($background-color);
  }

  @include flex-center-column;
  text-align: center;
  min-height: 100vh;
  font-size: $main-text-font-size;
  font-weight: $main-text-font-weight;

  header {
    @include themed() {
      background-color: t($background-color);
      border-bottom: 1px solid t($title-text-color);
      color: t($title-text-color);
    }
    @include sticky-top-element;
    @include flex-center;
    width: 100%;
    height: 10vh;
    z-index: 1;

    .change-theme-button {
      @include box(40px);
      position: absolute;
      right: 20px;

      // This button in particular is not themed because it's special and changes the theme
      background-color: transparent;
      border: 1px solid $title-text-color-light;
      color: $title-text-color-light;

      &:hover {
        background-color: $button-background-color-light;
        border-color: $button-background-color-light;
        color: $button-text-color-light;
      }

      &.dark-theme-active {
        box-shadow: none;
        background-color: $button-hover-background-color-dark;
        border: 1px solid $button-hover-background-color-dark;
        color: $button-hover-text-color-dark;
      
        &:hover {
          background-color: $dark-theme-button-hover-background-color;
          border-color: $dark-theme-button-hover-background-color;
        }
      }
    }
  }

  .content {
    margin-top: 10vh;
    width: 100%;
    
    .cards-container {
      @include flex-center;
      flex-wrap: wrap;
    }
  }
}
